<template>
  <div class="home">
    <v-row>
      <v-col cols="12">
        <v-data-table :items="participations" :headers="headers">
          <template #[`item.confirmed_status`]="{item}">
            {{ item.confirmed_status ? `Si` : `No` }}
          </template>

          <template #[`item.actions`]="{item}">
            <v-btn icon>
              <v-icon @click="goToExternal(item, true)">
                mdi-qrcode
              </v-icon>
            </v-btn>
          </template>
        </v-data-table>
      </v-col>
      <v-col cols="12">
        <v-data-table :items="tickets" :headers="headersTickets">
          <template #[`item.actions`]="{item}">
            <v-btn icon>
              <v-icon @click="goToExternal(item, false)">
                mdi-qrcode
              </v-icon>
            </v-btn>
            <v-btn icon v-if="item.order.facto_link">
              <v-icon @click="goToReceipt(item)">
                mdi-receipt
              </v-icon>
            </v-btn>
          </template>
        </v-data-table>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "Home",

  data() {
    return {
      headers: [
        { text: "Event", value: "event.name" },
        { text: "Confirmado", value: "confirmed_status" },
        { text: "Fecha Confirmacion", value: "confirmed_at" },
        { text: "Fecha Registro/Compra", value: "registered_at" },
        { text: "", value: "actions" }
      ],

      headersTickets: [
        { text: "Event", value: "ticket.event.name" },
        { text: "Ticket", value: "ticket.name" },
        { text: "Fecha Venta", value: "updated_at" },
        { text: "", value: "actions" }
      ]
    };
  },

  computed: {
    ...mapState(["participations", "tickets"])
  },

  methods: {
    goToExternal(item, confirmacion) {
      const url =
        window.location.protocol +
        "//" +
        window.location.hostname +
        (window.location.port ? ":8000" : "");
      if (confirmacion) {
        window.open(`${url}/qr/${item.uuid}`);
      } else {
        window.open(`${url}/qr/bought-ticket/${item.uuid}`);
      }
    },

    goToReceipt(item) {
      window.open(item.order.facto_link);
    }
  }
};
</script>

<style></style>
