<template>
  <v-app v-if="$auth.ready()">
    <v-app-bar app color="primary" dark>
      <div class="d-flex align-center">
        <v-img
          alt="Venoot Logo"
          class="shrink mr-2"
          contain
          :src="require('./assets/favicon.png')"
          transition="scale-transition"
          width="40"
        />

        VENOOT
      </div>

      <v-spacer></v-spacer>

      <template v-if="$auth.user()">
        <v-btn text @click="logout">LOGOUT</v-btn>
      </template>
    </v-app-bar>

    <v-main>
      <v-container>
        <router-view />
      </v-container>
    </v-main>
  </v-app>
  <v-app v-else>
    loading
  </v-app>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  name: "App",

  components: {},

  data: () => ({
    //
  }),

  methods: {
    ...mapMutations(["setParticipations", "setTickets"]),

    async logout() {
      this.loading = true;
      await this.$auth.logout();
      this.loading = false;
    }
  },

  async created() {
    this.loading = true;
    await this.$auth.load();

    if (this.$auth.check()) {
      if (this.$router.currentRoute.path !== "/home") {
        this.$router.push("/home");
      }

      const response = await this.axios.get("/participant-users/information");

      this.setParticipations(
        response.data.participants
          .map(participant => participant.participations)
          .flat()
          .filter(participation => !participation.event.estimate.ticket_sale)
      );

      this.setTickets(
        response.data.participants
          .map(participant => participant.participant_orders)
          .flat()
          .filter(order => order.status === 1)
          .map(order => {
            let tickets = order.participant_tickets;
            delete order.participant_tickets;
            for (let ticket of tickets) {
              ticket.order = order;
            }
            return tickets;
          })
          .flat()
      );
    }

    this.loading = false;
  }
};
</script>
