import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import router from "../router";
import auth from "@websanova/vue-auth";
import authBasic from "@websanova/vue-auth/drivers/auth/bearer";
import httpVueResource from "@websanova/vue-auth/drivers/http/axios.1.x";
import routerVueRouter from "@websanova/vue-auth/drivers/router/vue-router.2.x";

Vue.router = router;
Vue.use(VueAxios, axios);
Vue.use(auth, {
  auth: authBasic,
  http: httpVueResource,
  router: routerVueRouter,
  loginData: {
    url: "participant-users/auth/login",
    fetchUser: true,
    rememberMe: true
  },
  logoutData: {
    url: "participant-users/auth/logout",
    method: "POST",
    makeRequest: false
  },
  fetchData: { url: "participant-users/auth/me", method: "GET", enabled: true },
  refreshData: {
    url: "participant-users/auth/refresh",
    enabled: true,
    interval: 2
  }
});

/**
 * Now we will save the axios ajax information so we don't have to write it
 * all for every request
 */

Vue.axios.defaults.headers.common["Content-Type"] = "application/json";
Vue.axios.defaults.baseURL =
  process.env.NODE_ENV === "production"
    ? window.location.protocol +
      "//" +
      window.location.hostname +
      ":" +
      window.location.port +
      "/"
    : "http://localhost:8000/";
