<template>
  <v-card
    class="login my-12 mx-auto"
    max-width="600"
    elevation="8"
    :loading="loading"
  >
    <v-app-bar flat color="primary">
      <v-toolbar-title class="title white--text pl-0">
        LOGIN
      </v-toolbar-title>
    </v-app-bar>

    <v-card-text>
      <v-form>
        <v-row>
          <v-col cols="12"
            ><v-text-field label="E-mail" v-model="user.email"></v-text-field
          ></v-col>
          <v-col cols="12"
            ><v-text-field
              label="Password"
              type="password"
              v-model="user.password"
            ></v-text-field
          ></v-col>
        </v-row>
      </v-form>
    </v-card-text>

    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="success" @click="login" :loading="loading">ACEPTAR</v-btn>
    </v-card-actions>
  </v-card>
</template>

<script>
// @ is an alias to /src
import { mapMutations } from "vuex";

export default {
  name: "Home",
  data() {
    return {
      loading: false,
      user: {
        email: "",
        password: ""
      }
    };
  },

  methods: {
    ...mapMutations(["setParticipations", "setTickets"]),

    async login() {
      this.loading = true;
      await this.$auth.login({
        data: {
          ...this.user
        }
      });

      if (this.$auth.check()) {
        const response = await this.axios.get("/participant-users/information");

        this.setParticipations(
          response.data.participants
            .map(participant => participant.participations)
            .flat()
            .filter(participation => !participation.event.estimate.ticket_sale)
        );

        this.setTickets(
          response.data.participants
            .map(participant => participant.participant_orders)
            .flat()
            .filter(order => order.status === 1)
            .map(order => {
              let tickets = order.participant_tickets;
              delete order.participant_tickets;
              for (let ticket of tickets) {
                ticket.order = order;
              }
              return tickets;
            })
            .flat()
        );
        this.$router.push("/home");
      }

      this.loading = false;
    }
  }
};
</script>
