import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    participations: [],
    tickets: []
  },
  mutations: {
    setTickets(state, tickets) {
      state.tickets = tickets;
    },

    setParticipations(state, participations) {
      state.participations = participations;
    }
  },
  actions: {},
  modules: {}
});
